.title {
    align-items: center;
    color: #444;
    display: flex;
    grid-area: 2 / 2 / 3 / 3;
    justify-content: center;
    padding: 0.5rem;
    text-align: center;
  }
  
  @media (max-width: 650px) {
    .title {
      font-size: 0.85rem;
    }
  }
  
  @media (max-width: 500px) {
    .title {
      font-size: 0.75rem;
    }
  }